<template>
  <div class="text-h6">Use the buttons below to control the DeepPicar</div>
  <div class="row full-width justify-between">
    <q-btn
      color="primary"
      size="xl"
      padding="10px 50px"
      @mousedown="actuate('left')"
      @touchstart="actuate('left')"
      @mouseup="actuate('center')"
      @touchend="actuate('center')"
      icon="arrow_left"
    />
    <div class="flex-break"></div>

    <div class="column">
      <q-btn round color="primary" size="xl" @click="actuate('forward')" icon="north" />
      <q-btn round color="primary" size="xl" @click="actuate('stop')" icon="front_hand" />
      <q-btn round color="primary" size="xl" @click="actuate('reverse')" icon="south" />
    </div>
    <div class="flex-break"></div>
    <q-btn
      color="primary"
      size="xl"
      padding="10px 50px"
      @mousedown="actuate('right')"
      @touchstart="actuate('right')"
      @mouseup="actuate('center')"
      @touchend="actuate('center')"
      icon="arrow_right"
    />
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'ControlPad',
  created: function () {
    document.addEventListener('keydown', this.keyDown);
    document.addEventListener('keyup', this.keyUp);
  },
  methods: {
    actuate: function (direction) {
      axios.post('http://' + window.location.hostname + ':8000/actuate', { params: { direction: direction } })
        .then(response => this.responseData = response.data);
    },
    keyDown: function (k) {
      console.log(k)
      if (k.key == 'ArrowUp') {
        this.actuate('forward');
      }
      if (k.key == 'ArrowLeft') {
        this.actuate('left');
      }
      if (k.key == 'ArrowRight') {
        this.actuate('right');
      }
      if (k.key == 'ArrowDown') {
        this.actuate('reverse');
      }
      if (k.key == ' ') {
        this.actuate('stop');
      }
    },
    keyUp: function (k) {
      if (k.key == 'ArrowLeft' || k.key == 'ArrowRight') {
        this.actuate('center');
      }
    }
  }
}
</script>
